import { Link } from 'react-router-dom';
import './footer.css';
import React, { useState } from 'react';
import axios from 'axios';

const Footer = () => {

    const [inputValue, setInputValue] = useState('');

    const emailSubmitClick = () => {
        console.log("Input value:", inputValue);
    };

    const putDataInDatabase = async (e) => {
        e.preventDefault();
        try {
            const dataToSend = { email: inputValue };
            const response = await axios.post('https://digitalization-backend.onrender.com/register', dataToSend);
            console.log('Post created:', response.data);
            setInputValue('');

        } catch (error) {
            console.error('Error creating post:', error);
        }
    };

    return (
        <div id="footer">

            <div id="info">
                <div className="openingHours">
                    <div id="visitingBeiteddine">
                        Visiting Beiteddine
                    </div>
                    <div id="titleOpening">
                        Opening Times
                    </div>
                    <div id="message">
                        Monday closed
                    </div>
                    <div id="message">
                        Tuesday 9:00 AM – 18:00 PM
                    </div>
                    <div id="message">
                        Wednesday 9:00 AM – 18:00 PM
                    </div>
                    <div id="message">
                        Thursday 9:00 AM – 18:00 PM
                    </div>
                    <div id="message">
                        Friday 9:00 AM – 18:00 PM
                    </div>
                    <div id="message">
                        Saturday 9:00 AM – 18:00 PM
                    </div>
                    <div id="message">
                        Sunday 9:00 AM – 18:00 PM
                    </div>
                    <div id="telNumber">
                        <div>Beiteddine tel: +961 05 500 077</div>
                        <div>Our tel: +961 76 434 304</div>
                        <div>Email: smk60@mail.aub.edu</div>
                        <div>adk04@mail.aub.edu</div>
                    </div>

                </div>
                <div className="secondCol">
                    <div className="entryFees" >
                        <h3 id="entry">Entry Fees</h3>
                        <div className="fees">
                            <div className="fee-category">Lebanese Visitors:</div>
                            <div className="fee-amount">Adults: <span>100.000 LBP</span></div>
                            <div className="fee-amount">Students: <span>50.000 LBP</span></div>
                            <div className="fee-amount">Children (Under 12): <span>20.000 LBP</span></div>
                            <div className="fee-category">Non-Lebanese Visitors:</div>
                            <div className="fee-amount">Adults: <span>5$</span></div>
                            <div className="fee-amount">Children: <span>3$</span></div>
                        </div>
                    </div>

                    <div className="mailingList">
                        <h3 id="visitingBeiteddine">JOIN OUR MAILING LIST</h3>
                        <div id="titleOpening">Be the first to hear about our events, exhibitions and other activities</div>

                        <div class="footer-signup-textfield">
                            <input type="email" name="EMAIL" class="email" id="mce-EMAIL" placeholder="Your email address" required value={inputValue} onChange={(event) => setInputValue(event.target.value)}></input>
                        </div>
                        <div class="footer-signup-button">
                            <input type="submit" class="button secondary-button arrow-icon-button" value="Sign up" name="subscribe" id="mc-embedded-subscribe" onClick={putDataInDatabase}></input>
                        </div>


                    </div>
                </div>
                <div className="location">
                    <h3 id="visitingBeiteddine">Location</h3>

                    <div id="loc">Beiteddine Palace</div>
                    <div id="loc">Beiteddine</div>
                    <div id="loc">Chouf District</div>
                    <div id="loc">Lebanon</div>


                    <div id="maps">
                        <iframe
                            className="maps"
                            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3319.5042095074455!2d35.57744127545545!3d33.69589977329328!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x151f203c75459d93%3A0x339c929930abd754!2sBeiteddine%20Palace!5e0!3m2!1sen!2slb!4v1713903625031!5m2!1sen!2slb"
                            allowfullscreen=""
                            loading="lazy"
                            referrerpolicy="no-referrer-when-downgrade">
                        </iframe>
                    </div>
                </div>
            </div>
            <div id="footercontent">
                <div id="Logos">
                    <a href="https://www.aub.edu.lb/"><img src="images/aubfooterlogo.png" width="150px" /></a>
                    <a href="https://www.aub.edu.lb/fas/Pages/default.aspx"><img src="images/FASlogo.png" width="150px" /> </a>
                    <div id="allRightsReserved">
                        © American University Of Beirut. All rights reserved.
                    </div>
                    <br></br>
                    <div id="allRightsReserved">
                        Site by AUB Computer Science Students.
                    </div>
                </div>

            </div>
        </div>
    )
}

export default Footer;