
import React from 'react';
import Banner from '../Banner/Banner'

const Events = () => {
  return (

    <div>
      <Banner />

      <div class="timeline">


        <div class="event left">
          <div class="event-content">
            <div class="date">Dec 22, 2023🌟</div>
            <h2>Looking for Preexisting Scans</h2>
            <p> We contacted the Ministry of Tourism to request permission to capture
              images specifically by using a drone, but unfortunately, they refused our request
              due to security reasons and ongoing protests in the Lebanese public sector. Once
              we found VWL, we visited them as a team and each member was involved in
              asking them about the previous data collected and how we could build on their
              scans for our project. We ended up combining our photos and photos of the scans provided by VWL </p>
          </div>
        </div>
        <div class="event right" >
          <div class="event-content">
            <div class="date">Oct 17, 2023 💬</div>
            <h2>Photogrammetry with Metashape</h2>
            <p>For the photogrammetry with Metashape, each team member was involved in
              capturing images, processing them, and refining the 3D model of Beiteddine
              Palace. All of us made sure that we didn't miss the small details when it came to
              working with Metashape. We made sure to meet up to read together the
              Metashape guide to understand the steps needed to take to digitize Beiteddine.
              When we had our model, we decided to turn our Figma Model into a Website.
              We collaborated using the version control system GitHub, so each team member
            </p>
          </div>
        </div>

        <div class="event left" >
          <div class="event-content">
            <div class="date">Oct 17, 2023 🤖</div>
            <h2>Trip To Jordan</h2>
            <p>We also participated in a trip to Jordan from the 6th of November till the 13th of
              November, where we attended several workshops and went on many trips.
              visited Umm Qais, Jerash, Petra, and Wadi Rum the pics we took were of
              objects in Umm Qais, while taking the pics we varied the time of day, angles,
              frequency of images, and distance to objects we did so to determine the ideal
              combination of conditions to attain the most accurate model. We also attended
              conferences at JUST and GJU (German Jordanian University), where we could
              socialize and connect with people from diverse nationalities. We were also able
              to learn more about Metashape and Photogrammetry as a whole</p>
          </div>
        </div>
      </div>
    </div>

  )
}

export default Events;