import React, { useState, useEffect, useRef } from 'react';
import Banner from '../Banner/Banner.js';

function History() {
  const [currentIndex, setCurrentIndex] = useState(0);
  const images = [
    { src: "images/6.png", description: "Azure Mosaic Vase" },
    { src: "images/7.png", description: "Hand-Etched Brass Pot" },
    { src: "images/8.png", description: "Sacred Stone Tribute" },
    { src: "images/1.jpg", description: "Emir Bashir Chehab II" },
    { src: "images/2.jpg", description: "Fakhr-al-Din II" },
    { src: "images/3.jpg", description: "Fakhr-al-Din II" },
    { src: "images/6.png", description: "Azure Mosaic Vase" },
    { src: "images/7.png", description: "Hand-Etched Brass Pot" },
    { src: "images/8.png", description: "Sacred Stone Tribute" },
    { src: "images/1.jpg", description: "Emir Bashir Chehab II" },
    { src: "images/2.jpg", description: "Fakhr-al-Din II" },
    { src: "images/3.jpg", description: "Fakhr-al-Din II" },
    { src: "images/6.png", description: "Azure Mosaic Vase" },
    { src: "images/7.png", description: "Hand-Etched Brass Pot" },
    { src: "images/8.png", description: "Sacred Stone Tribute" },
    { src: "images/1.jpg", description: "Emir Bashir Chehab II" },
    { src: "images/2.jpg", description: "Fakhr-al-Din II" },
    { src: "images/3.jpg", description: "Fakhr-al-Din II" },
    { src: "images/6.png", description: "Azure Mosaic Vase" },
    { src: "images/7.png", description: "Hand-Etched Brass Pot" },
    { src: "images/8.png", description: "Sacred Stone Tribute" },
    { src: "images/1.jpg", description: "Emir Bashir Chehab II" },
    { src: "images/2.jpg", description: "Fakhr-al-Din II" },
    { src: "images/3.jpg", description: "Fakhr-al-Din II" },
    { src: "images/6.png", description: "Azure Mosaic Vase" },
    { src: "images/7.png", description: "Hand-Etched Brass Pot" },
    { src: "images/8.png", description: "Sacred Stone Tribute" },
    { src: "images/1.jpg", description: "Emir Bashir Chehab II" },
    { src: "images/2.jpg", description: "Fakhr-al-Din II" },
    { src: "images/3.jpg", description: "Fakhr-al-Din II" }
  ];

  const allImages = [...images, ...images];

  const delay = 2000; // Delay in milliseconds
  const transitionRef = useRef();

  useEffect(() => {
    transitionRef.current = handleTransition;
  });

  useEffect(() => {
    const interval = setInterval(() => {
      transitionRef.current(currentIndex);
    }, delay);

    return () => clearInterval(interval);
  }, [currentIndex]);

  const handleTransition = (currentIndex) => {
    setCurrentIndex(currentIndex + 1); // Move to next image continuously
  };

  const getWidth = () => 100 / images.length; // Width of each image in percentage

  return (
    <>
      <Banner />
      <div className="history-page">
        <h1 className="virtual">Virtual Tour</h1>
        <h3 className="enjoy">Enjoy a virtual tour of the Museum and check out some objects that are found within the walls of Beiteddine Palace.</h3>
        <div className="virtualTour">
          <iframe
            className="frame"
            name="yxb-jzh-hgp"
            src="https://framevr.io/yxb-jzh-hgp"
            height="570px"
            width="100%"
            allow="camera;microphone;display-capture;xr-spatial-tracking"
            scrolling="no"
            frameBorder="0"
            title="Virtual Reality Experience"
          ></iframe>
        </div>
        <h2 id="objectsFeatured">OBJECTS FEATURED</h2>
        <div className="image-gallery">
          <div className="image-gallery-inner" style={{ transform: `translateX(-${currentIndex * getWidth()}%)`, transition: 'transform 0.5s ease-out' }}>
            {allImages.map((image, index) => (
              <figure key={index}>
                <img src={image.src} alt={image.description} />
                <figcaption>{image.description}</figcaption>
              </figure>
            ))}
          </div>
        </div>
      </div>
    </>
  );
}

export default History;
