import { useState } from 'react';
import './cssimports.css';
import axios from 'axios';
import Banner from './components/Banner/Banner';
import AboutUS from './components/AboutUS/AboutUS';
import React from 'react';
import { HashRouter, Routes, Route, useLocation } from 'react-router-dom';
import Events from './components/Events/events';
import History from './components/History/History';
import PageHeader from './components/Page Header/PageHeader';
import ThreeDmodel from './components/3D model/ThreeDmodel';
import Footer from './components/Footer/footer';
import ChatBotComponent from './components/ChatBot/ChatBot';
import Room1 from './components/3D model/Room1';
import Exterior from './components/3D model/Exterior';
import Room2 from './components/3D model/Room2';

function App() {
  const [responseData, setResponseData] = useState(null);

  const fetchData = async () => {
    try {
      const response = await axios.get('https://localhost:4000/welcome');
      setResponseData(response.mssg);
    } catch (error) {
      console.error('Error fetching data:', error);
    }
  };

  return (
    <div className="App">
      <HashRouter>
        <Routes>
          <Route path='/pageHeader' element={<PageHeader />} />
          <Route path='/about' element={<AboutUS />} />
          <Route path='/location' element={<Events />} />
          <Route path='/threeDmodel' element={<ThreeDmodel />} />
          <Route path='/history' element={< History />} />
          <Route path='/room1' element={< Room1 />} />
          <Route path='/exterior' element={< Exterior />} />
          <Route path='/room2' element={< Room2 />} />
        </Routes>
        <ConditionalHeader />
        <ChatBotComponent />
        <Footer />
      </HashRouter>
    </div>
  );
}

function ConditionalHeader() {
  let location = useLocation();
  if (location.pathname === '/') {
    return <PageHeader />;
  }
  return null;
}

export default App;
